/* ----------------- Media queris
----------------------------------------------------------------------- */
@font-face {
  font-family: 'ns';
  src: url("./../src/fonts/NeoSansStd-Bold/NeoSansStd-Bold.eot?#iefix") format("embedded-opentype"), url("./../src/fonts/NeoSansStd-Bold/NeoSansStd-Bold.ttf") format("truetype"), url("./../src/fonts/NeoSansStd-Bold/NeoSansStd-Bold.otf") format("opentype"), url("./../src/fonts/NeoSansStd-Bold/NeoSansStd-Bold.woff") format("woff"), url("./../src/fonts/NeoSansStd-Bold/NeoSansStd-Bold.svg#NeoSansStd-Bold") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block; }

.svg__icon {
  display: inline-block;
  width: 30px;
  height: 30px;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor; }

html {
  box-sizing: border-box;
  font-size: 100%; }

*, *:before, *:after {
  box-sizing: inherit; }

* {
  padding: 0;
  margin: 0;
  text-decoration: none;
  list-style: none; }

.wrapper-inner {
  max-width: 1170px;
  margin: 0 auto; }

img {
  max-width: 100%;
  height: auto; }

.slick-slider {
  text-align: center; }
  .slick-slider .slick-slider-img {
    display: inline-block;
    vertical-align: middle;
    float: none; }

header.header-box {
  grid-area: header-box; }
  header.header-box .wrapper-inner .header-box__container {
    display: flex;
    position: relative;
    z-index: 2;
    justify-content: space-between;
    align-items: center;
    padding: 20px 5px; }
    header.header-box .wrapper-inner .header-box__container a.header-box__container--left {
      color: #fff;
      background: #ffffff url("./../src/img/bg-wire.svg") center;
      padding: 10px 10px 5px 10px; }
    header.header-box .wrapper-inner .header-box__container .header-box__container--right {
      display: flex;
      padding: 10px; }
      header.header-box .wrapper-inner .header-box__container .header-box__container--right a {
        font-size: 21px;
        padding: 10px;
        color: #ffffff; }

body {
  font-size: 100%;
  height: 100vh;
  font-family: "Roboto" sans-serif; }

main.grid-container {
  background: url("./../src/img/laptopOnTheTable.jpg") center;
  background-size: cover;
  background-color: #155799;
  background-image: linear-gradient(120deg, #155799, #159957);
  display: grid;
  position: relative;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: auto 1fr 1fr 1fr;
  height: 100vh;
  grid-template-areas: "header-box header-box header-box header-box header-box header-box"  "banner-box banner-box banner-box banner-box banner-box banner-box"  "banner-box banner-box banner-box banner-box banner-box banner-box"  "banner-box banner-box banner-box banner-box banner-box banner-box"; }

main.grid-container::after {
  content: "";
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%; }

.banner-box {
  grid-area: banner-box;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%; }
  .banner-box .wrapper-inner {
    width: 100%; }
    .banner-box .wrapper-inner .banner-box__container {
      display: flex;
      width: 100%;
      transition: all 0.3s ease-in-out; }
      .banner-box .wrapper-inner .banner-box__container .banner-box__container--item {
        position: relative;
        z-index: 2;
        flex: 1 1 calc(50% - 20px);
        background-size: cover;
        padding: 57px;
        background-color: #ffffff;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center; }
        .banner-box .wrapper-inner .banner-box__container .banner-box__container--item img {
          max-width: 100%;
          height: auto; }
      .banner-box .wrapper-inner .banner-box__container .banner-box__container--left {
        background-color: #cbcec3;
        cursor: pointer;
        position: relative; }
        .banner-box .wrapper-inner .banner-box__container .banner-box__container--left img {
          max-width: 45%;
          height: auto;
          position: absolute;
          left: 0;
          bottom: 0; }
        .banner-box .wrapper-inner .banner-box__container .banner-box__container--left:hover ~ .banner-box__container--right .hidden-code {
          background-color: #ffffff;
          visibility: visible;
          opacity: 1;
          transition: visibility 0s linear 0s, opacity .3s,padding .3s; }
      .banner-box .wrapper-inner .banner-box__container .banner-box__container--right {
        background: url("./../src/img/bg-wire.svg") center;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        position: relative; }
        .banner-box .wrapper-inner .banner-box__container .banner-box__container--right h1 {
          font-family: ns;
          color: #ffffff;
          font-size: 49px; }
        .banner-box .wrapper-inner .banner-box__container .banner-box__container--right h2 {
          color: #cecece;
          background-color: #303030;
          padding: 10px; }
        .banner-box .wrapper-inner .banner-box__container .banner-box__container--right .hidden-code {
          visibility: hidden;
          opacity: 0;
          background-color: #ffffff;
          position: absolute;
          transition: visibility 0s linear .3s, opacity .3s,padding .3s;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0; }
          .banner-box .wrapper-inner .banner-box__container .banner-box__container--right .hidden-code p.hidden-code-content {
            position: relative;
            overflow: auto;
            white-space: nowrap;
            border: none;
            padding: 2em;
            font-size: 1em;
            height: 100%;
            overflow-y: hidden;
            margin: auto; }
            .banner-box .wrapper-inner .banner-box__container .banner-box__container--right .hidden-code p.hidden-code-content span, .banner-box .wrapper-inner .banner-box__container .banner-box__container--right .hidden-code p.hidden-code-content strong {
              color: #155799;
              font-weight: 700; }
            .banner-box .wrapper-inner .banner-box__container .banner-box__container--right .hidden-code p.hidden-code-content .hover-email {
              color: #ff0028; }
            .banner-box .wrapper-inner .banner-box__container .banner-box__container--right .hidden-code p.hidden-code-content:hover .hover-email {
              color: #beef00; }
        .banner-box .wrapper-inner .banner-box__container .banner-box__container--right:hover .hidden-code {
          visibility: visible;
          opacity: 1;
          transition: visibility 0s linear 0s, opacity .3s,padding .3s; }

.grid-container-articles {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
  height: 300vh;
  grid-template-areas: "info info portfolio portfolio"  "info info portfolio portfolio"  "contact contact gd-gallery gd-gallery"  "contact contact gd-gallery gd-gallery"  "footer-box footer-box footer-box footer-box"  "footer-box footer-box footer-box footer-box"; }

.info {
  grid-area: info;
  background-color: #155799;
  display: flex;
  flex-wrap: wrap; }
  .info .info-item {
    flex: 0 0 calc(33.333% - 10px);
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #159957;
    margin: 5px; }
    .info .info-item img {
      max-width: 100%;
      height: auto;
      width: 100%;
      height: 100%;
      object-fit: cover; }

.portfolio {
  grid-area: portfolio;
  background-color: #beef00;
  display: flex;
  justify-content: center;
  align-items: center;
  background: url("./../src/img/laptop-glasses.jpeg") center;
  background-size: cover; }
  .portfolio p {
    background-color: #fff;
    padding: 20px;
    margin: 5px; }

.contact {
  grid-area: contact;
  background-color: #ff0028;
  background-color: #155799;
  background-image: linear-gradient(120deg, #155799, #159957);
  background: url("./../src/img/bg-main.jpeg") center;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center; }
  .contact p {
    background-color: #fff;
    padding: 20px;
    margin: 5px; }

.gd-gallery {
  grid-area: gd-gallery;
  background-color: #155799;
  background-image: linear-gradient(120deg, #155799, #159957);
  display: flex;
  justify-content: center;
  align-items: center; }

.footer-box {
  grid-area: footer-box;
  height: 100vh;
  background-color: #155799;
  display: flex;
  justify-content: center;
  align-items: center; }
